import React from "react";
import { Router, Switch, Route, Redirect } from "react-router-dom";
import { useSelector } from "react-redux";
import ProtectedRoute from "./protectedRoute";
import Header from "./header";
import Footer from "./footer";
import Search from "./searchResults";
import PostContent from "./postContent";
import Home from "./home";
import Profile from "./Profile";
import MainDiary from "./diary";
import Coming from "./coming";
import history from "../history";
import Alert from "./alert";
import Comments from "./comments";
import SignIn from "./signIn";
import SignUp from "./signUp";
import ChatRoom from "./chatRoom";
import CreateRoom from "./createRoom";
import SinglePost from "./singlePost";
const Routes = () => {
  const loggedIn = useSelector(({ auth }) => auth.isAuthenticated);
  return (
    <Router history={history}>
      <Alert />
      <Switch>
        <Route
          path={`/auth/signin`}
          exact
          component={() => (!loggedIn ? <SignIn /> : <Redirect to="/feed" />)}
        />
        <Route
          path={`/auth/signup`}
          exact
          component={() => (!loggedIn ? <SignUp /> : <Redirect to="/feed" />)}
        />
        <Route
          path={`/feed`}
          exact
          component={() => (
            <section>
              <Header />
              <Home />
              <Footer />
            </section>
          )}
        />
        <Route path={`/post/:id`} exact component={() => <SinglePost />} />
        <ProtectedRoute
          path={`/comments/:id`}
          exact
          component={() => (
            <section>
              <Header />
              <Comments />
              <Footer />
            </section>
          )}
        />
        <Route
          path={`/profile/:id`}
          exact
          component={() => (
            <section>
              <Header />
              <Profile />
              <Footer />
            </section>
          )}
        />
        <ProtectedRoute
          path={`/create/room`}
          exact
          component={() => (
            <section>
              <Header />
              <CreateRoom />
              <Footer />
            </section>
          )}
        />
        <ProtectedRoute
          path={`/results`}
          exact
          component={() => (
            <section>
              <Header />
              <Search />
              <Footer />
            </section>
          )}
        />
        <ProtectedRoute
          path={`/post`}
          exact
          component={() => (
            <section>
              <Header />
              <PostContent />
              <Footer />
            </section>
          )}
        />
        <ProtectedRoute
          path={`/diary`}
          exact
          component={() => (
            <section>
              <Header />
              <MainDiary />
              <Footer />
            </section>
          )}
        />
        <ProtectedRoute
          path={`/market`}
          exact
          component={() => (
            <section>
              <Header />
              <Coming />
              <Footer />
            </section>
          )}
        />
        <ProtectedRoute
          path={`/chat`}
          exact
          component={() => (
            <section>
              <Header />
              <ChatRoom />
              <Footer />
            </section>
          )}
        />
        <Route path="*" exact component={() => <Redirect to="/feed" />} />
      </Switch>
    </Router>
  );
};

export default Routes;
