import React, { useCallback, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { loadRoom } from "../actions";

import styles from "../styles/ChatRoom.module.css";

const ChatInformation = ({ id, setName }) => {
  const dispatch = useDispatch();
  const getRoom = useCallback(() => dispatch(loadRoom(id)), [dispatch, id]);
  useEffect(() => getRoom(), [getRoom]);
  const room = useSelector((state) => state.chatRooms.room);
  useEffect(() => setName(room.title), [room, setName]);
  const renderedProfilePhotos = [];
  for (let i = 0; i < 3 && room.users.length > i; i++) {
    renderedProfilePhotos.push(
      <section className={styles.dp} key={i}>
        <Link to={`profile/${room.users[i].user._id}`}>
          <img
            src={room.users[i].user.avatar}
            alt="Profile"
            className={`profile-photo`}
          />
        </Link>
      </section>
    );
  }
  return (
    <>
      <section className={styles.chatBox__headingContainer}>
        <section style={{ height: "100%", width: "100%" }}>
          <img
            src={room.profileImage}
            alt="Dp"
            className={`${styles.chatBox__image}`}
          />
          &nbsp;&nbsp;&nbsp;&nbsp;
          <h1
            className="heading_main"
            style={{
              display: "inline-block",
              margin: "0",
              textTransform: "capitalize",
            }}
          >
            {room.title}
            <br />
            <p className="note" style={{ fontSize: "1rem" }}>
              {room.users?.length} members
            </p>
          </h1>
        </section>
        <section className={styles.usersContainer}>
          {renderedProfilePhotos}
          <Link>
            <p className="note blue" style={{ fontSize: "0.8rem" }}>
              See All Users...
            </p>
          </Link>
        </section>
      </section>
      <br />
      <br />
    </>
  );
};

export default ChatInformation;
