import React, { useCallback, useEffect } from "react";
import { useDispatch } from "react-redux";
import { Helmet } from "react-helmet";
import { loadUser } from "../actions";
import "../styles/global.css";
import Routes from "./routes";

const App = () => {
  useEffect(() => {
    const datePassed =
      Date.parse(new Date()) - Date.parse(localStorage.getItem("expiryDate")) >
      0;
    if (datePassed) {
      localStorage.setItem("isAuthenticated", "false");
    }
  }, []);
  const dispatch = useDispatch();
  const getUser = useCallback(() => dispatch(loadUser()), [dispatch]);
  useEffect(() => {
    getUser();
  }, [getUser]);
  return (
    <section style={{ height: "100%" }}>
      <Helmet>
        <meta property="title" content="Tragency Village" />
        <meta
          property="quote"
          content="Tragency | Where traveller's feel at home"
        />
        <meta
          name="description"
          content="Remotely connecting like minded travelers'. Come & connect with fellow travelers & post cool travel experience to become travel influencer."
        />
        <meta
          property="image"
          content="https://res.cloudinary.com/tragencymedia/image/upload/v1633071748/dfdhcqlye7qj3wpwvtdy.jpg"
        />
        <meta property="og:locale" content="en_IN" />
        <meta property="og:type" content="website" />
        <meta property="og:title" content="Tragency Village" />
        <meta
          property="og:quote"
          content="Tragency | Where traveller's feel at home"
        />
        <meta
          property="og:hashtag"
          content="Tragency #travel #travelcommunity #travelfun #goa #beach #TragencyVillage"
        />
        <meta
          property="og:image"
          content="https://res.cloudinary.com/tragencymedia/image/upload/v1633071748/dfdhcqlye7qj3wpwvtdy.jpg"
        />
        <meta content="image/*" property="og:image:type" />
        <meta
          property="og:url"
          content="https://tragency-media.herokuapp.com/"
        />
        <meta property="og:site_name" content="Tragency" />
        <meta
          property="og:description"
          content="Remotely connecting like minded travelers'. Come & connect with fellow travelers & post cool travel experience to become travel influencer."
        />
        <title>Tragency Village</title>
      </Helmet>
      <Routes />
    </section>
  );
};
export default App;
